.guarantee{
    &__section-title{
        text-align: center;
    }
    &__container{
        padding: 50px 20px;
        background-color: #f1f1f1;
        position: relative;
    }
    &__list-item{
        padding-bottom: 20px;
        padding-left: 20px;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.66;
        border-left: 1px dashed #B9B9B9;
        color: #3D3D3D;
        &:last-child{
            padding-bottom: 0;
            border-left:none;
        }
        &::before{
            content: '';
            position: absolute;
            left: 14px;
            width: 15px;
            height: 20px;
            background-image: url('/images/guarantee/campfire.svg');
            background-size: cover;
        }
    }
}
//=============responsive-mobile==============//
@media screen and (max-width: 767px){
    .guarantee{
        &__section-title{
            font-size: 26px;
        }
        &__container{
            width: 100%;
        }
    }
}
//=============tablet==============//
@media screen and (min-width: 768px){
    .guarantee{
        &__list-item{
            padding-left: 25px;
            &::before{
            left: 74px;
            }
        }
        &__container{
            width: 100%;
            padding: 30px 50px 30px 80px;
        }
    }
}
//=============desktop================//
@media screen and (min-width: 1366px){
    .guarantee{
        &__list-item{
            padding-left: 40px;
            font-size: 20px;
            &::before{
                width: 22px;
                height: 30px;
                left: 105px;
            }
        }
        &__container{
            max-width: 1314px;
            padding: 45px 114px;
    
        }
    }
}